import WebApi from "api/WebApi";
export function UploadImage(fileData) {
    return WebApi.Put("images", fileData);
}
export function UploadAvatar(fileData) {
    return WebApi.Put("avatars", fileData);
}
export default {
    UploadImage,
    UploadAvatar
};



